<template>
  <div class="login-wrapper">
    <div class="login-container">
        <div class="login-text-input">
            <div class="login-text">
                Логин:
            </div>
            <input
                v-model="login"
                type="text">
        </div>
        <div class="login-text-input">
            <div class="login-text">
                Пароль:
            </div>
            <input
                v-model="password"
                type="password">
        </div>
        <button
            @click="signIn"
            class="login-button">
                Войти
        </button>

    </div>
  </div>
</template>

<script>
// import { mapActions } from 'vuex'
export default {
    data() {
        return {
            login: "",
            password: ""
        }
    },
    methods: {        
        // ...mapActions([
        //     "LOGINING"
        // ]),
        signIn(){
            this.$emit("signIn", {"login":this.login, "password": this.password})
            // this.LOGINING({"login":this.login, "password": this.password})

        }
    },
}
</script>

<style>
.login-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.4);
}
.login-container{
    /* width: 500px;
    height: 300px; */
    padding: 40px;
    background: rgb(176, 228, 252);
    border-radius: 3px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.login-text-input{
    display: flex;
    margin: 10px;
}
.login-text{
    width: 70px;
}
.login-button{
    width: 70px;
    align-self: center;
    margin-top: 20px;
}
</style>